<template>
  <div>
    <!-- loading -->
    <loading :active.sync="isLoading"></loading>

    <div class="text-right mt-5">
      <!-- <button data-toggle="modal" data-target="#productModal">bootstrapvue</button> -->
      <button class="btn btn-primary" @click="openmodal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td class="text-right">{{item.origin_price | currency}}</td>
          <td class="text-right">{{item.price | currency}}</td>

          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openmodal(false,item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="deletModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!--  -->
    <!-- <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pagination.has_pre}">
          <a class="page-link" href="#" aria-label="Previous"
          @click.prevent="getproducts(pagination.current_page-1)">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>

        <li class="page-item" v-for="page in pagination.total_pages" :key="page"
        :class="{'active': pagination.current_page ===page}">
        <a class="page-link" href="#" @click.prevent="getproducts(page)">{{page}}</a></li>

        
        <li class="page-item" :class="{'disabled': !pagination.has_next}">
          <a class="page-link" href="#" aria-label="Next"
          @click.prevent="getproducts(pagination.current_page+1)">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav> -->
 <Pagination :pages="pagination" @emitPages="getproducts"></Pagination>
    <!-- Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-spin" v-if="status.fileloading"></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  >
                  <!-- @change >  -->
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.imageUrl"
                  alt
                >
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  >
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder=""
                    >
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    >
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    >
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateproduct"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- delProductModal  -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deletproudct"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- end 2 modal-->
  </div>
</template>



<script>
import $ from "jquery";
import Pagination from './Pagination';
export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      isnew: false,
      isLoading: false,
      pagination: {},
      status: {
        fileloading: false
      }
    };
  },
  components: {
    Pagination,
  },
  methods: {
    getproducts(page = 1) 
    //getproductspage =1=1page  
    {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`; 
      ///products?page=:page${}
      const vm = this;
      // console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        // console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    openmodal(isnew, item) {
      if (isnew) {
        (this.tempProduct = {}), (this.isnew = true);
      } else {
        (this.tempProduct = Object.assign({}, item)), (this.isnew = false);
      }
      //tempProduct=itemObject.assignes6item
      $("#productModal").modal("show");
      //$import jquery
    },
    updateproduct() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      const vm = this;

      // MODAL
      let httpmethod = "post";

      if (!vm.isnew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpmethod = "put";
      }
      // console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      this.$http[httpmethod](api, { data: vm.tempProduct }).then(response => {
        // console.log(response.data);
        if (response.data.success) {
          $("#productModal").modal("hide");
          vm.getproducts();
        } else {
          $("#productModal").modal("hide");
          vm.getproducts();
          console.log("");
        }
        // vm.products = response.data.products;
      });
    },
    deletModal(item) {
      this.tempProduct = item;
      $("#delProductModal").modal("show");
    },
    deletproudct() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      this.$http.delete(api).then(response => {
        // console.log(response.data);
        if (response.data.success) {
          $("#delProductModal").modal("hide");
          vm.getproducts();
        } else {
          $("#delProductModal").modal("hide");
          vm.getproducts();
          console.log("");
        }
      });
    },
    uploadFile() {
      // console.log(this);
      const updatedd = this.$refs.files.files[0];
      const vm = this;
      const formdata = new FormData();
      formdata.append("file-to-upload", updatedd);
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/upload`;
      vm.status.fileloading = true;
      this.$http
        .post(url, formdata, {
          hearders: {
            "Content-Type": "multipaet/form-data"
          }
        })
        .then(response => {
          // console.log(response.data);
          vm.status.fileloading = false;
          if (response.data.success) {
            // vm.tempProduct.imageUrl = response.data.imageUrl;
            // console.log( vm.tempProduct);
            //>Vue$set
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
          } else {
            this.$bus.$emit("message:push", response.data.message, "danger");
          }
        });
    }
  },
  created() {
    this.getproducts();
  }
};
</script>



