<template>
    <div>
    <!-- loading -->
    <loading :active.sync="isLoading"></loading>

    <div class="text-right mt-5">
      <!-- <button data-toggle="modal" data-target="#productModal">bootstrapvue</button> -->
      <button class="btn btn-primary" @click="open(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="150"></th>
          <th width="100"></th>
          <th width="100"></th>
          <th width="80"></th>
          <th width="80"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>{{item.percent}}</td>
          <td>{{item.due_date}}</td>
          

          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="open(false,item)"></button>
            <!-- <button class="btn btn-outline-danger btn-sm" @click="deletModal(item)"></button> -->
          </td>
        </tr>
      </tbody>
    </table>
<!--  -->
    <Pagination :pages="pagination" @emitPages="getcoupons" v-if="pagination"></Pagination>
<!-- Modal -->
    <div
      class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0 ">
          <div class="modal-header ">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          
          <div class="modal-body">
            <div class="row">
              
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title"></label>
                  <!-- <input
                    type="text" class="form-control" id="title" placeholder=""> -->
                    <input
                    type="text" class="form-control" id="title"
                    v-model="tempCoupons.title" placeholder="">
                </div>
                
                  <div class="form-group">
                    <label for="code"></label>
                    <input
                      type="text" class="form-control" id="code"
                      v-model="tempCoupons.code"
                      placeholder="">
                  </div>
                  <div class="form-group">
                    <label for="number"></label>
                    <input
                      type="text" class="form-control" id="percent"
                      v-model="tempCoupons.percent"
                      placeholder="">
                  </div>

                  <div class="form-group">
                    <label for="due_date"></label>
                    <input type="date" class="form-control" id="due_date"
                      v-model="tempCoupons.due_date"
                      placeholder="">
                  </div>
                

                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled"
                      v-model="tempCoupons.is_enabled"
                      :true-value="1"
                      :false-value="0" >
                       <!-- <input class="form-check-input" type="checkbox" id="is_enabled"> -->
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer col-sm-12">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <!-- <button type="button" class="btn btn-primary" ></button> -->
            <button type="button" class="btn btn-primary" @click="newcoupon"></button>
          </div>
        </div>
      </div>
    </div>

    </div>
</template>

<script>
import $ from "jquery";
import Pagination from '../Pagination';
export default {
  data() {
    return {
      coupons: [],
      tempCoupons:{},
      isnew: false,
      isLoading: false,
      pagination: {},
      };
  },
  components: {
            Pagination
        },
  methods: {
    getcoupons(page = 1) 
    {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`; 
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.coupons = response.data.coupons;
        vm.isLoading = false;
      });
    },
     open(isnew, item) {
      if (isnew) {
        (this.tempCoupons = {}), (this.isnew = true);
      } else {
        (this.tempCoupons = Object.assign({}, item)), (this.isnew = false);
      }
      //tempProduct=itemObject.assignes6item
      $("#productModal").modal("show");
      //$import jquery
    },
    newcoupon(id){
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      const vm = this;
      // MODAL
      let httpmethod = "post";

      if (!vm.isnew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupons.id}`;
        httpmethod = "put";
      }
      
      this.$http[httpmethod](api, { data: vm.tempCoupons }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          $("#productModal").modal("hide");
          vm.getcoupons();
        } else {
          $("#productModal").modal("hide");
          vm.getcoupons();
          console.log("");
        }
        
      });
    },

    

   },
  created() {
    this.getcoupons();
  }
};
</script>


